import {init} from '../../untilities/structure'
import {Atom} from '../../init/05_atom'

// ----- Defining group ----- //
init.atoms.header = {};
let init_group = init.atoms.header;


init_group.Title = function()
{
	let features = {
		group: 'header',
		name: 'title',

		node_name: 'h1',
		addl_classes: 'homer',
		css: {},
	};

	return new Atom(features);
};



