import {init} from '../../untilities/structure'
import {Atom} from '../../init/05_atom'

// ----- Defining group ----- //
init.atoms.paragraph = {};
let init_group = init.atoms.paragraph;


init_group.Standard = function()
{
	let features = {
		group: 'paragraph',
		name: 'standard',

		node_name: 'p',
		addl_classes: '',
		css: {
			'padding-left': '20px'
		},
	};

	return new Atom(features);
};




